* {
  box-sizing: border-box; }

body {
  margin: 0;
  background-color: #fbfbfb;
  font-family: "IBM Plex Sans", Arial, sans-serif; }

h1 {
  font-weight: 500; }

h3 {
  margin-bottom: 40px; }

.button {
  display: table;
  padding: 12px 15px;
  margin-bottom: 10px;
  background: #0063ff;
  color: #fff;
  border: none;
  font-size: 15px;
  text-decoration: none; }

.bx--btn--primary {
  background: #0063ff;
  outline: none !important;
  border: none !important; }
  .bx--btn--primary:hover {
    background: #0059e4; }
  .bx--btn--primary:active {
    background: #003992; }

.bx--overflow-menu-options {
  outline: none; }

header.m-demo-header {
  margin-bottom: 20px;
  padding: 50px 0;
  text-align: center; }
  header.m-demo-header div.logo {
    border-radius: 20px;
    border: 1px solid #cad3e0;
    width: 100px;
    height: 100px;
    margin: auto;
    margin-bottom: 15px; }
    header.m-demo-header div.logo img {
      width: 75px;
      height: 75px;
      margin-top: 12.5px; }
  header.m-demo-header div.content {
    width: 80%;
    max-width: 800px;
    margin: auto; }
  header.m-demo-header div h1 {
    margin-bottom: 5px; }

.chart-type-selection {
  display: inline;
  line-height: 50px; }
  .chart-type-selection li {
    color: white;
    cursor: pointer;
    background-color: #009BEF;
    display: inline;
    padding: 5px 10px;
    margin: 5px;
    min-width: 50px; }
  .chart-type-selection .active {
    background-color: #FFB000; }
  .chart-type-selection .disabled {
    background-color: #959595;
    cursor: not-allowed; }

.resizable {
  resize: both; }

.demo-title {
  margin-top: 20px; }

.chart-holder {
  background-color: #fff;
  box-shadow: 0 15px 34px -11px rgba(22, 56, 107, 0.1);
  margin: 0 auto 60px auto;
  display: block;
  height: 500px;
  min-width: 300px;
  max-width: 800px;
  position: relative;
  transition: box-shadow .1s ease-out;
  overflow: hidden;
  resize: both;
  font-weight: 400; }
  .chart-holder h3 {
    border-bottom: 1px solid lightgray;
    margin: 0; }
  .chart-holder:hover {
    box-shadow: 0 15px 45px -11px rgba(22, 56, 107, 0.15);
    transition: box-shadow .1s ease-out; }
  .chart-holder:last-child {
    margin-bottom: 30px; }
  .chart-holder.has-actions {
    margin: auto; }

.chart-demo-actions {
  display: table;
  width: 100%;
  min-width: 300px;
  max-width: 800px;
  margin: auto;
  margin-top: -1px;
  margin-bottom: 45px;
  overflow: hidden; }
  .chart-demo-actions button {
    float: left; }

.chart-title {
  display: block;
  font-size: 16px;
  font-weight: bold; }

#classy-bar-chart-holder {
  overflow: hidden;
  resize: both; }

div#carbon-charts-patterns svg {
  height: 0; }
